@import './../common';
@import url(https://fonts.googleapis.com/css?family=Bad+Script);

@mixin theme($theme-name, $primary-darker, $primary-lighter, $secondary, $light) {
  &.#{$theme-name} {
    background-color: $light;
    color: $primary-darker;

    .title {
      background-color: $primary-lighter;
      color: $light;
    }

    .details {
      background-color: adjust-color($primary-lighter, $alpha: -0.9);
    }
  }
}

.fiction-card {
  display: grid;
  grid-template-rows: 150px repeat(4, auto);
  margin: 30px 0;
  border-radius: 2px;
  width: 100%;
  max-height: 700px;

  &.with-description {
    grid-template-rows: 150px repeat(5, auto);
  }

  .cover {
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .title,
  .categories,
  .date-published {
    text-align: center;
  }

  .title {
    margin: 0;
    padding: 10px;
  }

  .date-published,
  .categories {
    margin-top: 10px;
    font-size: small;
  }

  .categories {
    font-style: italic;
  }

  .details {
    display: grid;
    gap: 10px;
    margin: 10px 5% 0 5%;
    padding: 10px;

    P {
      margin: 0;
    }
  }

  .text {
    margin: 5%;
    overflow: auto;

    p {
      margin: 0;
    }
  }

  &.poetry {
    max-height: unset;

    .text {
      text-align: center;
      font-family: 'Bad Script', sans-serif;
      line-height: 1.2;
      overflow: visible;
    }
  }

  @include theme(reply, $primary-reply-darker, $primary-reply-lighter, $secondary-reply, $light-reply);
  @include theme(basil, $primary-basil-darker, $primary-basil-lighter, $secondary-basil, $light-basil);
}